body {
  margin: 0;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

/*/////////navigation////////////*/
.header {
  background-color: white;
  display: flex;
  justify-content: space-between;
  height: 3em;
  align-items: center;
}

.nav {
  position: relative;
  margin-right: 1em;

  ul {
    display: flex;
    list-style: none;
    gap: 2em;
    padding: 0;
    margin: 0;
  }

  a {
    color: black;
    text-decoration: none;
    font-size: large;
  }
}

.logo {
  color: #ffc0cb;
  text-shadow: 2px 1px 0px #9782dd;
  margin-left: 1em;
}

.mobileMenuButton {
  display: none;
}

/*///////////Splash////////////////////////*/
.img-splash {
  height: 40em;
  background-image: url("./images/1.jpg");
  background-size: cover;
  background-position: center;
}

/*//////////Items List//////////////////*/
.menu-items-wrapper {
  background-image: url("./images/dddepth-319.jpg");
  background-size: cover;
  background-position: center center;
  padding-bottom: 5em;
}

.menu-title {
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-size: 3em;
  color: #ffea61;
  margin: 0;
  padding: 1em;
  text-align: center;
}

.itemlist-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.card-container {
  background-color: #9782dd;
  margin: 2em;
  width: min-content;

  img {
    height: 20em;
  }
}

.item-name {
  color: rgb(255, 191, 0);
  margin-left: 1rem;
}

.item-price {
  font-size: 2.5em;
  color: white;
  margin-right: 1rem;
}

.description {
  display: none;

  p {
    color: white;
    margin: 0;
    padding: 1em;
  }
}

.description.expanded {
  display: block;
}

.more-button {
  margin-bottom: 1em;
  background-color: transparent;
  color: #eee;
  border: 1px solid #eee;
}

.row {
  display: flex;
  flex-direction: row;
  align-items: baseline;
  justify-content: space-between;
}

.solo-row {
  display: flex;
  justify-content: center;
}

/*///////////Footer/////////////*/
footer {
  background-color: cadetblue;
  padding: 20px;
  display: grid;
  justify-content: center;
  gap: 1em;

  p {
    font-size: 16px;
    color: #ffffff;
    margin: 0;
  }
}

/*///Mobile////*/
@media (max-width: 768px) {
  .splash-container {
    height: auto;
  }

  html {
    font-size: 12px;
  }

  .mobileMenuButton {
    display: block;
    margin-right: 1em;
  }

  .nav {
    display: none;
  }

  .nav.expanded {
    display: block;
    position: absolute;
    top: 3em;
    width: 100%;

    ul {

      display: grid;
      background-color: white;
    }

    a {
      display: block;
      width: 100%;
      text-align: center;
      text-decoration: none;
      font-size: large;
      padding: 2em 0;
    }

  }
}